.char{padding:15px 20px;border-bottom:1px solid #3d5a80;display:grid;grid-template-columns:auto 15% 20%;gap:15px;background-color:#fff}.char.increase .char__price,.char.increase .char__name{color:red}.char.like .char__thumb{transform:translateX(0);opacity:1}.char__name{line-height:35px;cursor:pointer}.char__price{line-height:35px;border:none}.char:last-child{border-bottom:none}.char__thumb{transition:.3s all;transform:translateX(60px);opacity:0;cursor:auto}.char__icons{display:flex;justify-content:flex-end;gap:10px}.char__icons img{width:30px;height:30px}.char__сookie,.char__recycle{transition:all .1s ease-out;cursor:pointer}.char__сookie:hover,.char__recycle:hover{transform:scale(1.1)}